		goNewDetails(e) {
			const that = this;
			const id = news.get_data_set(e, "id");
			const type = news.get_data_set(e, "type");
			let url = ''
			if (type == 0) {
				// 进入用户消息
				url = `/pages/newsChat/newsChat?id=${id}`
			} else {
				// 进入系统消息
				url = `/pages/sysNews/sysNews?id=${id}`
			}
			news.navigate_to(url);
		}
	},
